@value fontWeightStrong from '@/styles/typography.css';

.row {
    display: flex;
    flex-flow: row wrap;
    align-items:center;
}

.row > div {
    padding: 10px;
}

.name {
    flex-basis: 25%;
    font-weight: fontWeightStrong;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

.values {
    flex-grow: 1;
    text-align: right;
}

.values > div {
    line-height: 1.1em;
    padding: 5px;
}

.buttons {
}

.buttons a:not(:first-child) {
    display: none;
}

.width375 .values,
.width480 .values {
    display: flex;
}

.width375 .values > div,
.width480 .values > div {
    flex-basis: 50%;
}

.width480 .buttons a:not(:first-child) {
    display: inline;
}

.width480 .buttons a:first-child {
    display: none;
}

.width480 .buttons a:nth-child(2) {
    margin-right: 10px;
}




